<template>
  <div class='returnRequest'>
    <!-- 采购退货申请 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goList">采购退货申请</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{ $route.query.id ? '修改' : '添加' }}采购退货申请</el-breadcrumb-item>
    </el-breadcrumb>

    <div class='returnRequestBox'>
      <el-row>
        <el-col :span='24'>
          <div class='returnRequest_left'>
            <h3 class='rtitle'>
              服务类型： 退货
            </h3>
            <div class="instructions">
              <p>操作提示</p>
              <p>1.若您对订单进行支付后想取消购买且与商家达成一致退款，请填写“订单退款”内容并提交</p>
            </div>

            <div class='stepBar'>
              <el-steps :active="0" align-center finish-status="success">
                <el-step title="买家申请退款" description=""></el-step>
                <el-step title="商家处理退款申请" description=""></el-step>
                <el-step title="买家退货给商家" description=""></el-step>
                <el-step title="商家确认收货" description=""></el-step>
                <el-step title="平台审核" description=""></el-step>
              </el-steps>
            </div>

            <h3 class='rtitle'>
              请填写退货退款申请
            </h3>

            <el-form class='orderForm' :model="params" ref="orderForm" label-width='120px'>
              <el-form-item label="退货退款原因：" size="mini" prop="reason_id" :rules="{ required:true, message: '请选择退货退款原因',tiggle: 'blur' }">
                <el-select v-model="params.reason_id">
                  <el-option v-for="(val, key) in reason_list" :key='key' :label="val.reason_info" :value="val.reason_id"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="退款说明：" size="mini" prop="buyer_message" :rules="{ required:true, message: '请输入退款说明',tiggle: 'blur' }">
                <el-input v-model="params.buyer_message" :rows="4" type='textarea'></el-input>
              </el-form-item>
              <el-form-item label="退款金额：" size="mini" prop="refund_amount" :rules="{ required:true, message: '请输入退款金额',tiggle: 'blur' }">
                <el-input v-model="params.refund_amount" @blur="$publicFution.toFixedTwo(params, 'refund_amount',  {type:'Positive', max: params.goods_pay_price,message:'退款金额不能超过可退'})"></el-input>
                (最多 <span class='mainGreen'>{{ params.goods_pay_price }}</span>元)
                <p>退款金额不能超过可退</p>
              </el-form-item>

              <el-form-item label="退货数量：" size="mini" prop="goods_num" :rules="{ required:true, message: '请输入退货数量',tiggle: 'blur' }">
                <el-input v-model="params.goods_num" @blur="$publicFution.toFixedTwo(params, 'goods_num', { type: 'PositiveInteger',max: params.maxGoodsNum,message: '退货数量不能超过可退' })"></el-input> (最多可退 <span class='mainGreen'>{{ params.maxGoodsNum }}</span>个)
              </el-form-item>

              <el-form-item label="上传凭证：" size="mini" prop="name">
                <cupload v-model="params.upload_pic" :limit='3' :extendParams='extendParams' returnStr='response.data.oss_file_path' @success='success' url='/service_station/open/Upload/uploadImage'>
                  <el-button type='primary' style='width: 100%;'>选择文件</el-button>
                </cupload>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
      </el-row>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "purchasePurchaserefundReturnRequestAdd",
  data() {
    return {
      extendParams: {
        directory: "return_voucher"
      },
      params: {
        goods_num: ""
      }
    };
  },
  methods: {
    goList () {
      this.$router.push({ path: "/purchase/returnRequest" });
    },
    goBack() {
      this.$EventBus.$emit("closeTab", this.$router);
    },
    getData() {
      if (this.$route.query.id) {
        this.$axMethod
          .serviceApplyReturn({
            ec_order_sn: this.$route.query.id,
            goods_id: this.$route.query.goods_id
          })
          .then(({ data }) => {
            if (data.code == 200) {
              this.params = data.data.order;
              this.params.maxGoodsNum = this.params.goods_num;
              this.params.ec_order_sn = this.$route.query.id;
              this.reason_list = data.data.reason_list;
            } else {
              this.goBack();
            }
          });
      }
    },
    submitForm() {
      this.$refs.orderForm.validate(valid => {
        if (valid) {
          var obj = {
            ec_order_sn: this.params.ec_order_sn,
            reason_id: this.params.reason_id,
            buyer_message: this.params.buyer_message
              ? this.params.buyer_message
              : "   ",
            upload_pic: this.params.upload_pic,
            goods_id: this.$route.query.goods_id,
            refund_amount: this.params.refund_amount,
            goods_num: this.params.goods_num
          };
          this.$axMethod.serviceSubmitReturn(obj).then(({ data }) => {
            if (data.code == 200) {
              this.$message.success(data.msg);
              this.goBack();
            }
          });
        }
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang='less' scoped>
.returnRequest {
  .returnRequestBox {
    .rtitle {
      margin-bottom: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #dddddd;
    }
    padding: 10px;
    .returnRequest_left {
      .instructions {
        padding: 10px;
        background-color: #fcf8e3;
        color: #c09853;
        font-weight: bold;
        line-height: 25px;
      }
      .stepBar {
        margin: 40px;
      }
      /deep/ .el-form-item--mini.el-form-item {
        width: 100%;
      }
    }
  }
}
</style>